$("._tab").click(function() {
    let parentBlock = $(this).parents("._tabs-parent")
    let tabId = $(this).attr("data-tab")
    $(parentBlock).find("._tab").removeClass("_active")
    $(this).addClass("_active")
    $(parentBlock).find(".tab-content").removeClass("_active")
    $(parentBlock).find(`.${tabId}`).addClass("_active")
})

$(".qa-card__show").click(function () {
    let parent = $(this).parents(".qa-card")

    if($(parent).hasClass("_active")){
        $(".qa-card").removeClass("_active")
        $(".qa-card__hidden").slideUp()
    } else{
        $(".qa-card").removeClass("_active")
        $(".qa-card__hidden").slideUp()
        $(parent).addClass("_active")
        $(parent).find(".qa-card__hidden").slideDown()
    }
})

setTimeout(function (){
    mainCardAnimate($(".main-card:nth-child(1)"),1)
},2000)


function mainCardAnimate(el,num){
    $(el).addClass("_active")
    setTimeout(function () {
        $(el).removeClass("_active")
        if(num === 5){
            num = 1
        } else{
            num++
        }
        mainCardAnimate($(`.main-card:nth-child(${num})`),num)
    },1000)
}

let phone = document.querySelector('.phone');

let Visible = function(target) {
    // Все позиции элемента
    let targetPosition = {
            top: window.pageYOffset + target.getBoundingClientRect().top,
            left: window.pageXOffset + target.getBoundingClientRect().left,
            right: window.pageXOffset + target.getBoundingClientRect().right,
            bottom: window.pageYOffset + target.getBoundingClientRect().bottom
        },
        // Получаем позиции окна
        windowPosition = {
            top: window.pageYOffset,
            left: window.pageXOffset,
            right: window.pageXOffset + document.documentElement.clientWidth,
            bottom: window.pageYOffset + document.documentElement.clientHeight
        };
    if (targetPosition.bottom - 10 > windowPosition.top &&
        targetPosition.top - 30 < windowPosition.bottom) {
        let video = target.querySelector(".video-custom-play");
        console.log(video)
        target.classList.add("in-view")
        playVideo(video)
    } else {

    }
};

window.addEventListener('scroll', function() {
    initAnimElements()
});
initAnimElements()

function initAnimElements() {
    Visible(phone)
}